.card-image {
    display: flex;
    flex-direction: column;
}

#default-image {
    padding-top: 150px;
    width: inherit;
}

#default-msg {
    padding-top: 50px;
    padding-bottom: 20px;
    font-family: Verdana, sans-serif;
    font-weight: bold;
    font-size: medium;
    width: 100%;
    text-align: center;
    
}

#table-image {
    object-fit: cover;
    width: 100%;
}

#table-h3 {
    margin-top: 10px;
    text-align: center;
    font-size: x-large;
    color: #373837;
}